              <desktopCardComponent
                :data="slotProps.data"
              ></desktopCardComponent>
            </div>
          </template>
        </DataView>
      </div>
    </div>
    <div class="p-lg-2 p-d-sm-none p-d-md-none p-d-lg-inline-block"></div>
  </div>
</template>

