<template>
  <div class="p-d-inline-block div-card">
    <Card>
      <template #header>
        <div class="card-header p-p-3">
          <router-link :to="{ name: 'Anunt', params: { id: data.id } }">
            <img
              :alt="data.altText"
              :src="
                `${publicPath}` + 'images/cardPresentationImages/' + data.image
              "
              class="card-image"
            />
          </router-link>
        </div>
      </template>
      <template #title>
        <h2 class="card-title">Simple Card</h2>
        <div class="card-short-data">
          <div class="created-by">
            <span>
              <img
                class="created-by-image"
                :src="data.createdByImageSrc"
                alt=""
              />
            </span>
            <span> {{ data.createdByName }} </span>
          </div>
          <span class="views">
            <i class="pi pi-eye"></i>
            {{ data.views }}
          </span>
        </div>
      </template>
      <template #content>
        <h3 class="description">
          {{ data.description }}
        </h3>
      </template>
    </Card>
  </div>
</template>

<script>
export default {
  name: "desktopCardComponent",
  props: ["data"],
  data() {
    return {
      publicPath: process.env.BASE_URL,
    };
  },
};
</script>
<style scoped>
.card-image {
  max-width: 200px;
