  width: 100%;
}
.card-header {
  text-align: center;
  height: 230px;
}

.p-card {
  margin: 0.5rem;
  border: 1px solid #dee2e6;
}
.card-short-data {
  font-weight: 400;
  font-size: 16px;
  margin: 0 0 0 0%;
  color: #2f4550;
  text-align: left;
}
.views {
  display: block;
}
.created-by {
  display: inline;
  vertical-align: middle;
}
.description {
  font-weight: unset;
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
}
.card-title {
